<template>
	<div class="star" :class="starType">
		<span class="star-item" :class="item" v-for="(item,index) in starClasses" :key="index"></span>
	</div>
</template>

<script>
	const CLS_ON = 'on'  	//高亮
	const CLS_HALF = 'half' //半颗星星
	const CLS_OFF = 'off'   //不亮的星星
	const LENGTH = 5;       //默认显示5颗星星

	export default {
		props:{
			score: Number,  //评分值
			size: Number	//尺寸
		},
		computed:{
			//计算星星的尺寸
			starType(){
				return 'star-'+this.size; 	 //star-24 
			},
			starClasses(){
				//根据分值，计算显示几颗全亮、是否显示半颗、几颗不亮
				//例如：3.8，3颗全亮，1颗半亮 1颗不亮，转换成 将小数转换成： x.5   
				let score = Math.floor(this.score * 2) / 2;  // 3.5  3    
				//显示几颗全亮的星星
				let integer = Math.floor(score);
				//是否有小数,确定显示几颗半亮
				let hasDecimal = score % 1 !== 0;   
				//判断显示几颗不亮的星星
				
				let result = [];
				for(let i=0;i<integer;i++){
					result.push(CLS_ON)
				}

				if(hasDecimal){
					result.push(CLS_HALF)
				}
				while(result.length < LENGTH){
					result.push(CLS_OFF)
				}

				return result;   // ['on','on','on','half','off']
			}
		}
	}
</script>

<style lang="less" scoped>
	.star{
		font-size:0;
		.star-item{
			display:inline-block;
			background-repeat: no-repeat;		
		}
		&.star-24{
			.star-item{
				width: 10px;
				height: 10px;
				margin-right: 3px;
				background-size: 10px 10px;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background-image:url('star24_on@2x.png')
				}
				&.half{
					background-image:url('star24_half@2x.png')
				}
				&.off{
					background-image:url('star24_off@2x.png')
				}
			}			
		}
		&.star-36{
			.star-item{
				width: 15px;
				height: 15px;
				margin-right: 6px;
				background-size: 15px 15px;
				&:last-child{
					margin-right: 0;
				}
				&.on{
					background-image:url('star36_on@2x.png')
				}
				&.half{
					background-image:url('star36_half@2x.png')
				}
				&.off{
					background-image:url('star36_off@2x.png')
				}
			}
		}
		&.star-48{
			.star-item{
				width: 20px;
				height: 20px;
				margin-right: 9px;
				background-size: 20px 20px;
				&:last-child{
					margin-right: 0;
				}
				background-size: 20px 20px;
				&.on{
					background-image:url('star48_on@2x.png')
				}
				&.half{
					background-image:url('star48_half@2x.png')
				}
				&.off{
					background-image:url('star48_off@2x.png')
				}
			}
		}
	}
</style>


