<template>
	<div>
		<header>
			<div class="location">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="icon-location">
					<path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z">
					</path>
				</svg>
				<span>河北软件职业技术学院(东校区)</span>
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="icon-arrow">
					<path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 		0-.372.628.412 1.412l5.176 5.176z">
					</path>
				</svg>
			</div>
		</header>
		<section class="search">
			<div class="button">
				<a href="">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="index-2iXz3_0">
						<path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z">
						</path>
					</svg>
					<span>搜索饿了么商家、商品名称</span>
				</a>
			</div>
		</section>
		<section class="hot-search">
			<ul>
				<li v-for="(item,index) in hotsearch">{{item.word}}</li>
			</ul>
		</section>
		<!--食品分类轮播图-->
		<section class="entries">
			<svg v-if="entries.length==0" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/">
			</svg>
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item,index) in entries" :key="index">
					<a href="" v-for="(item2,index2) in item" :key="index2">
						<img :src="item2.image_hash | imgUrl" alt="">
						<span>{{item2.name}}</span>
					</a>
				</mt-swipe-item>
			</mt-swipe>
		</section>
		<!--推荐商家-->
		<section class="recommends">
			<!-- 标题 -->
			<section class="shop-title">
				<h3>推荐商家</h3>
			</section>
			<section class="shop-list">
				<div class="shop-info" v-for="(item,index) in restaurants" :key="index">
					<!-- logo部分 -->
					<div class="shop-logo">
						<img :src="item.restaurant.image_path|imgUrl" alt="">
					</div>

					<!-- 右边主体部分 -->
					<div class="shop-main">
						<!-- 商家名称 -->
						<div class="title">
							<h3>{{item.restaurant.name}}</h3>
							<ul>
								<li>广告</li>
								<li>保</li>
							</ul>
						</div>
						
						<!-- 商家评分 -->
						<div class="rating">
							<div class="ratingWrap">
								<elm-star :score="4.7" :size="24"></elm-star>
								<span>{{item.restaurant.rating}}</span>
								<span>月售{{item.restaurant.recent_order_num}}单</span>
							</div>
							<!-- 配送方式 -->
							<div class="deliver-mode">
								<span>蜂鸟专送</span>
							</div>
						</div>

						<!-- 配送信息 -->
						<div class="deliver">
							<div class="money">
								<span>¥{{item.restaurant.float_minimum_order_amount}}起送</span>
								<span>免配送费</span>
							</div>
							<div class="distance">
								<span>{{item.restaurant.distance/1000 | Mydecimal}}km</span>
								<span>{{item.restaurant.order_lead_time}}分钟</span>
							</div>
						</div>

						<!-- 商家活动 -->
						<div class="shop-activity">
							<div class="activities">
								<div class="activityList" v-for="(item,index) in item.restaurant.activities" :key="index">
									<span style="background-color: rgb(240, 115, 115);color:#fff">{{item.icon_name}}</span>
									<span>{{item.tips}}</span>
								</div>
							</div>
							<div class="activityBtn">
								<span>{{item.restaurant.activities.length}}个活动</span>
								<img src=" ">
							</div>
						</div>
					</div>
				</div>

			</section>
		</section>
		<elm-tabbar></elm-tabbar>
	</div>
</template>

<script>
	import Tabbar from "@/components/footer/Tabbar"
	import {getHotSearch,getEntries,getRestaurants} from "@/api/msite"
	import _ from "lodash"

	import Vue from "vue"
	import {Swipe,SwipeItem} from 'mint-ui';

	import "mint-ui/lib/style.css"
	import "mint-ui/lib/index.js"

	Vue.component(Swipe.name, Swipe);
	Vue.component(SwipeItem.name, SwipeItem);

	import Star from "@/components/star/Star"

	export default{
		components: {
			//给Tabbar组件取别名,elm-tabbar
			"elm-tabbar" : Tabbar,
			"elm-star":Star
		},
		data(){
			return{
				hotsearch:[],
				entries:[],
				restaurants:[],
				page:1,
				limit:10
			}
		},
		filters:{
			imgUrl(src){
				let url = "https://fuss10.elemecdn.com/";
				url += src.slice(0,1)+"/"+src.slice(1,3)+"/"+src.slice(3);
				let reg = /jpeg|jpg|png$/;
				let result = url.match(reg);
				console.log(result[0]);
				url += "."+result[0]+"?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/";
				return url;
			},
			Mydecimal(data){
				return data.toFixed(2);
			}
		},
		created(){
			getHotSearch(40.1144418,116.3579348)
			.then((data) => {
				console.log(data.data)
				this.hotsearch = data.data;
			})
			.catch((err) => {
				console.log(err);
			})

			//获取分类轮播图数据
			getEntries()
			.then((data) =>{
				// console.log(data.data[0].entries)
				this.entries = _.chunk(data.data[0].entries,8);
				console.log(this.entries)
			})
			.catch((err) =>{
				console.log(err);
			})
			//获取商家列表
			getRestaurants()
			.then((res) =>{
				console.log(res.data.items)
				this.restaurants = res.data.items;
			})
			.catch((err) =>{
				console.log(err)
			})
		}
	}
</script>

<style lang="less" scoped>
	*{box-sizing: border-box;}
	header{
		height: 1.333333rem;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		display: flex;
		align-items: center;
		padding-left: 0.346667rem;
		.location{
			display: flex;
			align-items: center;
			.icon-location{
				width: 0.4rem;
				height: 0.426667rem;
			}
			.icon-arrow{
				width: 0.186667rem;
				height: 0.106667rem;
			}
			span{
				font-size: 0.453333rem;
				color: #fff;
				width: 80%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				margin-left: 0.1.333333rem;
				font-weight: 700;
			}
		}
	}
	.search{
		width: 100%;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		padding: 0.5rem 0.346667rem 0.346667rem;
		display: flex;
		align-items: center;
		justify-content: content;
		.button{
			width: 100%;
			height: 0.986667rem;
			line-height: 0.986667rem;
			background: #fff;
			a{
				display: flex;
				align-items: center;
				justify-content: center;
				text-decoration: none;
				svg{
					width: 0.386667rem;
					height: 0.386667rem;
				}
				span{
					font-size: 0.373333rem;
					color: #999;
					margin-left: 0.16rem;
				}
			}
		}
	}
	.hot-search{
		height:1.12rem;
		background-image:linear-gradient(90deg,#0af,#0085ff);
		width:100%;
		padding:0.0rem 0.346667rem;
		ul{
			width:100%;
			overflow-x:auto;
			white-space:nowrap;
			&::-webkit-scrollbar{
				display:none;
			}
			li{
				font-size:0.32rem;
				display:inline-block;
				color:#fff;
				&:not(:last-child){
					margin-right:0.48rem;
				};
			}
		}
	}
	.entries{
		height:4.533333rem;
		a{
			text-decoration:none;
			display:flex;
			flex-direction:column;
			align-items:center;
			width:25%;
			float:left;
			margin-top:0.373333rem;
			img{
				width:0.853333rem;
				height:0.853333rem;
			}
			span{
				font-size:0.32rem;
				color:#666;
				margin-top:0.266667rem;
			}
		}
	}
// 推荐商家
	.recommends{

		.shop-title h3{
			font-size: .4rem;
			height: 1.333333rem;
			display: flex;
			justify-content: center;
			align-items: center;

			&:before,&:after{
				content: "";
				display: block;
				width: .533333rem;
				height: .026667rem;
				background-color: #999;
			}
			&:before{
				margin-right: .346667rem;
			}
			&:after{
				margin-left: .346667rem;
			}
		}

		// 商家列表
		.shop-info{
			
			display: flex;
			justify-content: flex-start;
			align-items: stretch;
			margin-bottom:.346667rem;

			.shop-main{
				
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				flex-grow: 1;
				// 商家名称
				.title{
					display: flex;
					justify-content: space-between;
					h3{
						font-size: .4rem;
						color: #333;
						font-weight: 700;
					}
					ul{
						display: flex;
						align-items: center;
						li{
							font-size: 0.266667rem;
						}
					}
				}
				// 商家评分
				.rating{
					display: flex;
					justify-content: space-between;
					span{
						font-size: .293333rem;
					}
					.deliver-mode span{
						background-image: linear-gradient(45deg,#0085ff,#0af);
						color: #fff;
					}
				}
				//配送信息
				.deliver{
					display: flex;
					justify-content: space-between;
					span{
						font-size: .293333rem;
					}
				}
				// 商家活动
				.shop-activity{
					display: flex;
					justify-content: space-between;
					span{
						font-size: .293333rem;
					}
				}
			}
		}
	}
</style>


